body {
  background-image: url('../assets/background.jpg');
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: .8
}

.App {
  text-align: center;
  padding: 5%;
}

.right-button {
  cursor: pointer;
  float: right;
}

.right-button span {
  border-radius: 50%;
  color: #FFF;
  font-size: 1.3em;
  padding: 0 4px
  
}

.right-button span.green {
  background: limegreen;
  opacity: .7;
}

.right-button span.red {
  background: red;
  opacity: .7
}

.right-button span:hover {
  opacity: 1
}

.list-group {
  -webkit-box-shadow: 4px 4px 5px rgba(0, 0, 0, .3);
          box-shadow: 4px 4px 5px rgba(0, 0, 0, .3);
}

.list-item {
  display: inline-block;
}



